html, body {
    overflow-x: hidden;
}

.container {
    padding: 0 20px;
    width: 100%;
    max-width: 100%;
}

.bg-orange {
    background-color: #FFAC70;
}

.bg-pink {
    background-color: #FF7979;
}

.bg-green-light {
    background-color: #7FD59C;
}

.bg-gray-light2 {
    background-color: #EDF3F9;
}

@media screen and (min-width: 1024px) {
    .container {
        padding: 0 30px;
    }
}

@media screen and (min-width: 1440px) {
    .container {
        max-width: 1500px;
    }
}

.swiper-custom .swiper {
    padding-bottom: 60px;
}
swiper-custom .swiper-pagination {
    bottom: 0;
}

.swiper-custom .swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    margin: 0 6px !important;
    background: #D3DCE5;
    opacity: 1;
}

.swiper-custom .swiper-pagination-bullet-active {
    background-color: #122940;
}

.link-underline {
    border-bottom-width: 0;
    background-image: linear-gradient(transparent, transparent), linear-gradient(#fff, #fff);
    background-size: 0 1px;
    background-position: 0 100%;
    background-repeat: no-repeat;
    transition: background-size .3s ease-in-out;
}

.link-underline-show {
    border-bottom-width: 0;
    background-image: linear-gradient(transparent, transparent), linear-gradient(#fff, #fff);
    background-size: 100% 1px;
    background-position: 0 100%;
    background-repeat: no-repeat;
    transition: background-size .3s ease-in-out;
}

.link-underline-black {
    background-image: linear-gradient(transparent, transparent), linear-gradient(#122940, #122940)
}

.link-underline:hover {
    background-size: 100% 1px;
    background-position: 0 100%;
}

.link-underline-show:hover {
    background-size: 0 1px;
    background-position: 0 100%;
}
